#left-side {
  position: relative;
  z-index: 2;
  width: 50%;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 2% 4%;
  background: rgba(0, 0, 0, 0.2);
  padding-top: 15vh;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
span {
  color: white;
  font-family: 'Montserrat', sans-serif;
}
.name {
  font-size: 5vw;
  margin: 0;
}
#social {
  width: 100%;
  display: flex;
  margin: auto auto 5vh auto;
  justify-content: center;
}
#social > a:not(:last-child) {
  margin-right: 4vw;
}
.social-element {
  background: white;
  border-radius: 50%;
  width: 4vw;
  height: 4vw;
  display: flex;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
  color: black;
}
.social-element i {
  display: block;
  margin: auto;
  font-size: 2vw;
  transition: all 0.2s ease-in-out;
}
.social-element:hover {
  background: #ee6c4d;
  transition: all 0.2s ease-in-out;
}
.social-element:hover i {
  color: white;
  transition: all 0.2s ease-in-out;
}

@media only screen and (max-width: 1000px) {
  #left-side {
    width: 100%;
    text-align: center;
    padding-top: 4%;
    padding: 4%;
  }
  .name {
    font-size: 10vw;
  }
  #changing-title {
    margin-top: 0;
  }
  #changing-title h1 {
    font-size: 5vw;
  }
  #dynamic-title-container {
    margin-top: 5px;
  }
  .social-element {
    width: 35px;
    height: 35px;
  }
  .social-element i {
    font-size: 4vw;
  }
  #social {
    margin: auto auto 5px auto;
  }
}
